.modal-video-recipes-close-button-icon {
    display: flex;
    justify-content: center;
    background-color: #FFFFFF;
    fill: var(--dark-text-color);
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.modal-video-recipes-close-button{
    background-color: #FFF;
    padding: 2px;
    border: none;
margin-top: 14px;
margin-left: 491px;
position:absolute;
}
.backdrop-video-recipes {
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    background-color: rgba(46, 47, 66, 0.4);
    backdrop-filter: blur(3px);
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.backdrop-video-recipes.active{
    visibility: visible;
    opacity: 1; 
    overflow-y: auto;
}
.modal-video-recipe {
    position: relative;
    top: auto;
    width: 531px;
    max-width: 531px;
    height: auto;
    display: flex;
    flex-direction: column;
    background: #fff;
    border-radius: 15px;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
    overflow-y: scroll;
}
.modal-video-recipe.active {
    visibility: visible;
    opacity: 1;
}
