.rating__body::before {
    content: '★★★★★';
    display: block;
    color: var(--primary-text-CH-color-STARS);
    font-size: 16px;
  }
  
  .rating__active {
    position: absolute;
    width: 0;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
  }
  .rating__body{
    position: relative;
  }
  .rating__active::before {
    content: '★★★★★';
    position: absolute;
    width: 100%;
