.chief-img {
  width: 182px;
  height: 272px;
  position: absolute;
  overflow: hidden;
  border-radius: 15px;
}
.mk-photo-wrapper {
  height: 442px;
  width: 137px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  background-color: var(--dark-text-color);
  position: relative;
}
