.mk-info {
  display: flex;
  position: absolute;
  flex-direction: column;
  gap: 8px;
  bottom: 14px;
  left: 14px;
}
.mk-name {
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  text-align: center;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.4;
}
.mk-region {
  color: rgba(255, 255, 255, 0.2);
  text-align: center;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.5;
}
